<template>
  <div class="about" v-if="post">
    <h1>{{$route.params.id}}</h1>
    <h1>{{post.title}}</h1>
    <p>{{post.body}}</p>
  </div>
</template>



<script>
export default {
  data() {
    return {
      post: null
    }
  },
  created() {
    fetch(`https://jsonplaceholder.typicode.com/posts/${this.$route.params.id}`).then((response) => {
      response.json().then((data) => {
        this.post = data;
      })
    })
  }
}
</script>

<style>
  .home-header {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>